import Vue from 'vue'
import VueRouter from 'vue-router'
Vue.use(VueRouter)

const routes = [
  {
    path: '/index',
    name: 'index',
    meta:{title:"首页"},
    component: ()=>import('../views/indexPage'),
    children:[
      {
        path: 'xueYuan',
        name: 'XueYuan',
        meta:{title:"学院概况"},
        component:()=>import('../views/others/XueYuan')
      },
      {
        path: 'dangQun',
        name: 'DangQun',
        meta:{title:"党群工作"},
        component:()=>import('../views/others/DangQun')
      },
      {
        path: "guiZhang",
        name: "GuiZhang",
        meta:{title:"规章制度"},
        component:()=>import('../views/others/GuiZhang')
      },
      {
        path: "peiXun",
        name: "PeiXun",
        meta:{title:"培训教育"},
        component:()=>import('../views/others/PeiXun')
      },
      {
        path: "xueLi",
        name: "XueLi",
        meta:{title:"学历教育"},
        component:()=>import('../views/others/XueLi')
      },
      {
        path: 'lianXi',
        name: 'LianXi',
        meta:{title:"联系我们"},
        component:()=>import('../views/others/LianXi')
      },
      {
        path: 'zhengWen',
        name: 'ZhengWen',
        meta: {title: "正文"}
      }
    ],
  },
  {
    path: '/',
    redirect:'/index',
  }


]

const router = new VueRouter({
  mode: 'history',
  base: process.env.BASE_URL,
  routes
})

export default router
